<template>
  <div id="marquee">
    <span id="marquee_text">{{content}}</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: "",
      speed: 20 //速度数值越大速度越慢
    };
  },
  methods: {
    marquee(p, s) {
      let scrollWidth = document.getElementById(p).offsetWidth;
      let textWidth = document.getElementById(s).clientWidth;
      let i = scrollWidth;
      (function change() {
        i-=4;
        if (i < -textWidth) {
          i = scrollWidth;
        }
        document.getElementById(s).style.transform = `translateX(${i}px)`;
        window.requestAnimationFrame(change);
      })();
    },
    setNewHot() {
      this.$x
        .get(this.url.newHot, {})
        .then(res => {
          this.coleeList = res.data;
          this.coleeList.forEach(ele => {
            let str = ele + "\xa0\xa0\xa0\xa0\xa0\xa0\xa0";
            this.content += str;
          });
        })
        .then(res => {
          this.marquee("marquee", "marquee_text");
        });
    }
  },
  mounted() {
    this.setNewHot();
  }
};
</script>


<style lang='scss' scoped>
#marquee {
  // color: red;
  display: block;
  width: 96%;
  height: 130px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

#marquee_text {
  display: block;
  height: 100%;
  line-height: 130px !important;
  position: absolute;
  z-index: 999999;
  color: #fff;
  font-size: 54px;
  top: 0;
  // left: 100%;
  transform:translateX(100%);
  line-height: 30px;
  display: block;
  word-break: keep-all;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>